@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0/css/all.min.css');
@import url('https://fonts.googleapis.com/css2?family=Lekton:wght@400;700&family=Waiting+for+the+Sunrise&display=swap');
*{
  margin:0px;
  padding:0px;
}
Body{
  background: linear-gradient(#f7f4e8 , #f7f4e8 85%, #ceccc2 90%, #bebdb4);
  color:#1a1a1a;
  width:100vw;
  height:100vh;
  &:after{
    z-index: -1;
    content:"";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    opacity:0.3;
    filter:grayscale(100%);
    background-image: url("./Assets/paper-texture.jpg");
    background-size:cover;
    background-blend-mode: darken;
  }
  display:flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  overflow:hidden;
  font-family: "Waiting for the Sunrise","Lekton", sans-serif;
  font-size:1.2rem;
  -webkit-tap-highlight-color: transparent;
  overflow:hidden;
}
:root{
  --shadow: 0.2;
}
.playgroundContainer{
  position:absolute;
  left:0;
  top:0;
  width:100vw;
  height:100vh;
  display:flex;
  justify-content: center;
  align-items: flex-start;
  overflow:hidden;
  .introText{
    margin-top:15vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.1rem;
    animation: introText 0.5s linear;
    animation-delay:2s;
    animation-fill-mode: forwards;
    opacity:0;
    @keyframes introText{
      from{opacity:0}
      to{opacity:1}
    }
    .name{
      overflow:hidden;
      white-space: nowrap;
      text-align:center;
      animation: typewriter 1s steps(35, end);
      animation-fill-mode: forwards;
      opacity: 0;
      animation-delay:2s;
      @keyframes typewriter {
        from{width:0;opacity:1;}
        to{width:100%;opacity:1;}
      }
    }
    .inputBox{
      display:flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      >h2{
        overflow:hidden;
        white-space: nowrap;
        opacity:0;
        animation: typewriter 1s steps(35, end);
        animation-delay:4s;
        animation-fill-mode: forwards;
        margin-right:1rem;
      }
      >input{
        border:none;
        height:2rem;
        width:0rem;
        background-color: transparent;
        caret-color: #ffa500;
        font-size:1.2rem;
        font-weight: 700;
        text-align: center;
        font-family: "Waiting for the Sunrise","Lekton", sans-serif;
        &:focus{
          outline:none;
        }
        animation: fadeIn 1s linear;
        animation-fill-mode: forwards;
        animation-delay: 5.5s;
        @keyframes fadeIn {
          0%{width:0;
            border-bottom:2px solid #08080875;}
          75%{width:10rem;
              border-bottom:2px solid #08080875;}
          100%{
            width:10rem;
            border-bottom:2px solid #08080875;
          }
          
        }
      }
    }
  }
  .tutorialBox{
    margin-top:15vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.1rem;
    .friendBox{
      display:flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      >h2{
        overflow:hidden;
        white-space: nowrap;
        opacity:0;
        text-align:center;
        animation: typewriter 1s steps(35, end);
        animation-fill-mode: forwards;
        @keyframes typewriter {
          from{width:0;
            opacity:1;}
          to{width:100%; 
            opacity:1;}
        };
        &:nth-child(2){
          animation-duration:2s;
          animation-delay:1.5s;
        }
      }
    }
    .normalName{
      overflow:hidden;
      white-space: nowrap;
      opacity:0;
      text-align:center;
      animation: typewriter 1s steps(35, end);
      animation-fill-mode: forwards;
      @keyframes typewriter {
        from{width:0;
          opacity:1;}
        to{width:100%; 
          opacity:1;}
      }
    }
    .introSum{
      margin-bottom:1rem;
      overflow:hidden;
      white-space: nowrap;
      opacity:0;
      text-align:center;
      animation: typewriter 2s steps(35, end);
      animation-fill-mode: forwards;
      animation-delay: 1.5s;
      @keyframes typewriter {
        from{width:0;
          opacity:1;}
        to{width:100%; 
          opacity:1;}
      }
    }
    .guide{
      margin-bottom:1rem;
      overflow:hidden;
      white-space: nowrap;
      opacity:0;
      text-align:center;
      animation: typewriter 2s steps(35, end);
      animation-fill-mode: forwards;
      animation-delay: 4s;
      >span{
        font-weight:600;
      }
      @keyframes typewriter {
        from{width:0;
          opacity:1;}
        to{width:100%; 
          opacity:1;}
      }
    }
    .warning{
      overflow:hidden;
      white-space: nowrap;
      opacity:0;
      text-align:center;
      animation: typewriter 2s steps(35, end);
      animation-fill-mode: forwards;
      animation-delay: 6s;
      color:#741717;
      font-weight:700;
      @keyframes typewriter {
        from{width:0;
          opacity:1;}
        to{width:100%; 
          opacity:1;}
      }
    }
    .introSumF{
      @extend .introSum;
      animation-delay: 4s;
    }
    .guideF{
      @extend .guide;
      animation-delay:6.5s;
    }
    .warningF{
      @extend .warning;
      animation-delay: 9s;
    }
  }
  .einContainer{
    padding: 5rem 5rem 0 5rem;
    position:absolute;
    bottom:0;
    width:15rem;
    height:22.5rem;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    animation: dropEin 0.5s linear, bounce 1s linear;
    cursor:grab;
    user-select: none;
    &:before{
      position:absolute;
      content:'';
      bottom:5.25rem;
      width:8rem;
      height:1.5rem;
      background:radial-gradient(#080808 40%, #0808083d 60%, transparent 70%, transparent);
      opacity:0;
      border-radius: 50%;
      animation: op 0.1s linear;
      animation-delay: 0.4s;
      animation-fill-mode: forwards;
      @keyframes op {
        from{opacity:0}
        to{opacity:var(--shadow)}
      }
    }
    @keyframes dropEin {
      from{
        transform: translateY(-100vh)
      }
      to{
        transform: translateY(0);
      }
    }
    @keyframes bounce{
      50%{height:22.5rem; width:15rem}
      60%{height:16.5rem;width:12rem}
      70%{height:24.5rem;width:17rem}
      80%{height:18.5rem;width:14rem}
      90%{height:23.5rem;width:16rem}
      100%{height:22.5rem;width:15rem}
    }
    .headPart{
      position:relative;
      display:flex;
      flex-direction: row;
      justify-content: center;
      align-items: flex-start;
      width:100%;
      height:50%;
      .leftHead{
        width:25%;
        height:100%;
        .leftEarBox{
          position:relative;
          left:30%;
          width:100%;
          height:30%;
          display:flex;
          justify-content: flex-end;
          align-items: flex-end;
          z-index: 1;
          .leftEar{
            z-index:1;
            position:absolute;
            right:0;
            width:70%;
            transform-origin: right 70%;
            animation: leftE 0.1s linear;
            animation-iteration-count: 5;
            @keyframes leftE {
              0%{
                transform: rotate(35deg) skew(10deg);

              }
              100%{
                transform: rotate(45deg) skewX(10deg);
              }
            }
          }
        }
        .leftBeardBox{
          position:relative;
          left:45%;
          width:100%;
          height:70%;
          display:flex;
          justify-content: flex-end;
          align-items: center;
          .leftBeard{
            z-index: 1;
            position:absolute;
            right:0;
            width:65%;
            transform-origin: right 12.5%;
            animation: leftB 0.1s linear;
            animation-iteration-count: 5;
            @keyframes leftB {
              0%{
                transform: scaleY(-1) rotate(30deg)skewX(0);
              }
              100%{
                transform: scaleY(-1) rotate(30deg) skewX(15deg);
              }
            }
          }
        }
      }
      .middleHead{
        width:50%;
        height:100%;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .topBox{
          position:relative;
          width:100%;
          height:33%;
          display:flex;
          justify-content: center;
          align-items: flex-end;
          .top{
            position:absolute;
            transform:perspective(10rem);
            transform-origin: center bottom;
            height:85%;
            animation: topH 0.1s linear;
            animation-iteration-count: 5;
            @keyframes topH {
              0%{
                transform:perspective(10rem) rotateX(-10deg) rotateY(20deg);
              }
              100%{
                transform:perspective(10rem) rotateX(-10deg) rotateY(-20deg)
              }
            }
          }
        }
        .mouthBox{
          position:relative;
          width:100%;
          height:33%;
          display:flex;
          justify-content: center;
          align-items: center;
          .mouth{
            position:absolute;
            height:60%;
            animation: mouth 0.1s linear;
            animation-iteration-count: 5;
            @keyframes mouth {
              0%{transform: scaleY(1);}
              100%{transform: scaleY(0.7)}
  
            }
          }
        }
        .chinBox{
          position:relative;
          bottom:5%;
          width:100%;
          height:34%;
          display:flex;
          justify-content: center;
          align-items: flex-start;
          .chin{
            z-index:1;
            position:absolute;
            height:60%;
            transform-origin: center top;
            animation:chin 0.1s linear;
            animation-iteration-count: 5;
            @keyframes chin {
              0%{transform: skewX(10deg);}
              100%{transform:skewX(-10deg);}
            }
          }
        }
      }
      .rightHead{
        width:25%;
        height:100%;
        .rightEarBox{
          position:relative;
          right:30%;
          width:100%;
          height:30%;
          display:flex;
          justify-content: flex-start;
          align-items: flex-end;
          transform: scaleX(-1);
          z-index: 1;
          .rightEar{
            z-index: 1;
            transform-origin: right 70%;
            position: absolute;
            right:0;
            width:70%;
            animation: rightE 0.1s linear;
            animation-iteration-count: 5;
            @keyframes rightE {
              0%{
                transform:  rotate(35deg) skew(10deg);
              }
              100%{
                transform:rotate(45deg) skewX(10deg);
              }
            }
          }
        }
        .rightBeardBox{
          position:relative;
          right:45%;
          width:100%;
          height:70%;
          display:flex;
          justify-content: flex-start;
          align-items: center;
          transform:scaleX(-1);
          .rightBeard{
            z-index: 1;
            position:absolute;
            right:0;
            transform-origin: right 12.5%;
            width:65%;
            animation: rightB 0.1s linear;
            animation-iteration-count: 5;
            @keyframes rightB {
              0%{
                transform: scaleY(-1) rotate(30deg) skewX(0);
              }
              100%{
                transform: scaleY(-1) rotate(45deg) skewX(15deg);
              }
            }
          }
        }
      }
    }
    .bottomPart{
      position:relative;
      bottom:10%;
      display:flex;
      flex-direction: row;
      justify-content: center;
      align-items: flex-start;
      width:100%;
      height:40%;
      opacity: 0.8;
      .leftArmBox{
        position:relative;
        left:5%;
        width:32.5%;
        height:100%;
        display:flex;
        align-items: flex-start;
        justify-content: flex-end;
        .leftArm{
          z-index: 1;
          position:absolute;
          right:0;
          width:50%;
          transform-origin: top right;
          animation: leftArm 0.1s linear;
          animation-iteration-count: 5;
          @keyframes leftArm {
            0%{
              transform: scaleY(-1) rotate(25deg);
            }
            100%{
              transform: scaleY(-1) rotate(15deg);
            }
          }
        }
      }
      .middleBottom{
        width:35%;
        height:100%;
        .torsoBox{
          z-index: -1;
          position:relative;
          display:flex;
          align-items: flex-start;
          justify-content: center;
          height:55%;
          width:100%;
          .chest{
            position:absolute;
            top:0;
            width:70%;
            filter:brightness(0);
          }
        }
        .legsContainer{
          height:45%;
          width:100%;
          display:flex;
          flex-direction:row;
          justify-content: space-between;
          align-items: flex-start;
          .leftLegBox{

            z-index:1;
            position:relative;
            bottom:41%;
            left: 2%;
            width:50%;
            height:100%;
            display:flex;
            justify-content: flex-start;
            align-items: flex-start;
            .leftLeg{

              z-index:1;
              position:absolute;
              left:0;
              width:65%;
              transform-origin: 70% top;
              animation: leftLeg 0.1s linear;
              animation-iteration-count: 5;
              @keyframes leftLeg {
                0%{transform:rotate(35deg);}
                100%{transform:rotate(25deg);}
              }
            }
          }
          .rightLegBox{
            z-index: 1;
            position:relative;
            bottom:41%;
            right:2%;
            width:50%;
            height:100%;
            transform:scaleX(-1);
            display:flex;
            justify-content: flex-end;
            align-items: flex-start;
            .rightLeg{
              z-index: 1;
              position:absolute;
              left:0;
              width:65%;
              transform-origin: 70% top;
              animation: rightLeg 0.1s linear;
              animation-iteration-count: 5;
              @keyframes rightLeg {
                0%{transform:rotate(35deg);}
                100%{transform: rotate(25deg);}
              }
            }
          }
        }
      }
      .rightArmBox{
        position:relative;
        right:5%;
        width:32.5%;
        height:100%;
        transform:scaleX(-1);
        display:flex;
        align-items: flex-start;
        justify-content: flex-start;
        .rightArm{
          z-index: 1;
          position:absolute;
          right:0;
          transform-origin: right top;
          width:50%;
          animation: rightArm 0.1s linear;
          animation-iteration-count: 5;
          @keyframes rightArm {
            0%{
              transform: scaleY(-1) rotate(25deg);
            }
            100%{
              transform: scaleY(-1) rotate(15deg);
            }
          }
        }
      }
    }
  }
  .factBox{
    margin-top:15vh;
    width:100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.1rem;
    .factIntro{
      width:90%;
      text-align: center;
      white-space: nowrap;
      overflow:hidden;
      margin-bottom:1rem;
      font-size:2.25rem;
      animation: factTitleType 2s linear;
      animation-fill-mode: forwards;
      @keyframes factTitleType {
        from{width:0;opacity:1;}
        to{width:100%;opacity:1;}
      }
    }
    .factText{
      width:90%;
      text-align: center;
      padding-left:5%;
      padding-right:5%;
      opacity:0;
      animation: fadeFact 0.5s linear;
      animation-fill-mode: forwards;
      animation-delay: 1.5s;
      @keyframes fadeFact {
        from{opacity:0}
        to{opacity:1}
      }
    }
  }
  .InsultBox{
    position:absolute;
    top:15vh;
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    color:#741717;
    letter-spacing: 0.1rem;
    >h2{
      width:90%;
      text-align: center;
      animation: insultType 0.5s linear;
      animation-fill-mode: forwards;
      font-size:2rem;
      @keyframes insultType {
        from{opacity:0;}
        25%{opacity:1}
        50%{opacity:0}
        75%{opacity:1}
        85%{opacity:0}
        to{opacity:1;}
      }
    }
  }
  .aboutMeContainer{
    z-index:2;
    position:absolute;
    width:100vw;
    .openBtn{
      width:100%;
      display:flex;
      justify-content: flex-end;
      >h3{
        letter-spacing: 0.1rem;
        top:1rem;
        right:2rem;
        position:absolute;
        font-weight:700;
        cursor:pointer;
        transition:color 0.2s;
        &:hover{
          color:#6b6b6b;
        }
        &:active{
          color:#6b6b6b;
        }
      }
    }
    .openedAboutMe{
      height:100vh;
      width:100vw;
      top:0;
      left:0;
      background: linear-gradient(#f7f4e8 , #f7f4e8 85%, #ceccc2 90%, #bebdb4);
      animation: amFlip 0.25s linear;
      animation-fill-mode: forwards;
      &:after{
        z-index: 0;
        content:"";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        opacity:0.3;
        filter:grayscale(100%);
        background-image: url("./Assets/paper-texture.jpg");
        background-size:cover;
        background-blend-mode: darken;
      }
      @keyframes amFlip {
        from{opacity:0}
        to{opacity:1}
      }
      .closeBtn{
        z-index:3;
        letter-spacing: 0.1rem;
        top:1rem;
        right:2rem;
        position:absolute;
        font-weight:700;
        cursor:pointer;
        transition:color 0.2s;
        &:hover{
          color:#6b6b6b;
        }
        &:active{
          color:#6b6b6b;
        }
      }
      .content{
        z-index:2;
        width:100%;
        height:100%;
        display:flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        .imgBox{
          z-index:2;
          width:50%;
          height:100%;
          display:flex;
          justify-content: flex-end;
          align-items: center;
          >img{
            width:75%;
            transform:scaleX(-1);
            opacity:0;
            animation: contentAni 0.5s linear;
            animation-fill-mode: forwards;
            animation-delay:0.5s;
            @keyframes contentAni {
              from{opacity:0}
              to{opacity:1}
            }
          }
        }
        .textBox{
          z-index:2;
          width:50%;
          height:100%;
          display:flex;
          flex-direction:column;
          justify-content: center;
          align-items: flex-start;
          .textBoxContent{
            width:75%;
            display:flex;
            flex-direction:column;
            justify-content: center;
            align-items: center;
            opacity:0;
            animation: textAni 0.5s linear;
            animation-fill-mode: forwards;
            animation-delay:1.25s;
            @keyframes textAni {
              from{opacity:0}
              to{opacity:1}
            }
            >h2{
              letter-spacing: 0.1rem;
              text-align: center;
              margin-bottom:1.5rem;
              font-size: 1.25rem;
              font-weight: 500;
              >span{
                font-weight:800;
                font-size: 2rem;
              }
            }
            >h4{
              font-size:1.5rem;
            }
            >p{
              text-align: center;
              font-size:1.25rem;
              &:nth-child(4){
                color:#741717;
              }
            }
            .linksBox{
              margin-top:1.5rem;

              >a{
                text-decoration: none;
                color:#080808;
                font-size:2rem;
                margin-left:1rem;
                margin-right:1rem;
                transform: rotate(50deg);
                transition: color 0.2s;
                &:hover{
                  color:#6b6b6b;
                }
              }
            }
          }
        }
      }
    }
  }
}
@media only screen and (max-width : 992px) {
  .playgroundContainer{
    overflow:hidden;
    .introText{
      margin-top:12vh;
    }
    .tutorialBox{
      margin-top:12vh;
    }
    .factBox{
      margin-top:12vh;
      width:100%;
      .factIntro{
        animation: factTitleType 1.5s linear;
      }
      .factText{
        text-align: center;
        width:100%;
        padding-left:5%;
        padding-right:5%;
      }
    }
    .InsultBox{
      position:absolute;
      top:12vh;
      >h2{
        width:90%;
        padding-left: 5%;
        padding-right:5%;
      }
    }
    .aboutMeContainer{
      z-index:2;
      position:absolute;
      width:100vw;
      .openBtn{
        >h3{
          &:hover{
            color:#080808;
          }
          &:active{
            color:#6b6b6b;
          }
        }
      }
      .openedAboutMe{
        .content{
          flex-direction: column;
          .imgBox{
            display:flex;
            flex-direction:column;
            justify-content: flex-end;
            align-items: center;
            width:100%;
            height:45%;
            margin-bottom:5%;
            >img{
              width:50%;
            }
          }
          .textBox{
            z-index:2;
            width:100%;
            height:50%;
            justify-content: flex-start;
            align-items: center;
            .textBoxContent{
              .linksBox{
                >a{
                  &:hover{
                    color:#080808;
                  }
                  &:active{
                    color:#6b6b6b;
                  }
                }
              }
            }
  
          }
        }
      }
    }
  }
}
