        padding-right:5%;
      }
    }
    .InsultBox{
      position:absolute;
      top:12vh;
      >h2{
        width:90%;
        padding-left: 5%;
        padding-right:5%;
      }
    }
    .aboutMeContainer{
      z-index:2;
      position:absolute;
      width:100vw;
      .openBtn{
        >h3{
          &:hover{
            color:#080808;
          }
          &:active{
            color:#6b6b6b;
          }
        }
      }
      .openedAboutMe{
        .content{
          flex-direction: column;
          .imgBox{
            display:flex;
            flex-direction:column;
            justify-content: flex-end;
